const initialState = {
  loading: false,
  totalSupply: 0,
  cost: 0,
  error: false,
  errorMsg: "",
};

const dataReducer = (state = initialState, action) => {
  switch (action.type) {
    case "SMART_CONTRACT_DATA_REQUEST_PENDING":
      return {
        ...state,
        loading: true,
        error: false,
        errorMsg: "",
      };
    case "SMART_CONTRACT_DATA_REQUEST_SUCCESS":
      return {
        ...state,
        loading: false,
        totalSupply: action.payload.totalSupply,
        // cost: action.payload.cost,
        error: false,
        errorMsg: "",
      };
    case "SMART_CONTRACT_DATA_REQUEST_FAIL":
      return {
        ...initialState,
        loading: false,
        error: true,
        errorMsg: action.payload,
      };
    default:
      return state;
  }
};

export default dataReducer;
